import React, { useEffect, useState } from "react";
import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Container,
  createTheme,
  CssBaseline,
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  ThemeProvider,
  Typography,
} from "@mui/material";
import instance from "../axios.create";

const theme = createTheme();

export default function Album() {
  const [rows, setrows] = useState([]);
  const [open, setOpen] = useState(false);
  const [selectedCard, setSelectedCard] = useState(null);

  useEffect(() => {
    instance.post("info_lsit").then((response) => {
      setrows(response.data);
    });
  }, []);

  const handleViewClick = (card) => {
    setSelectedCard(card);
    setOpen(true);
  };

  const handleClose = () => {
    setSelectedCard(null);
    setOpen(false);
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      <main>
        {/* Hero unit */}
        <Container sx={{ py: 8 }}>
          <Grid container spacing={4} justifyContent="center" alignItems="center">
            {rows.map((card) => (
              <Grid item key={card.id_info} xs={12} sm={6} md={4}>
                <Card
                  sx={{
                    height: "100%",
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <CardMedia
                    component="img"
                    image="https://source.unsplash.com/random"
                    alt="random"
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h5" component="h2">
                      {card.heading}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button
                      size="small"
                      onClick={() => handleViewClick(card)}
                      variant="contained"
                      sx={{ borderRadius: 5 }}
                    >
                      
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>


        <Dialog
          open={open}
          onClose={handleClose}
          aria-describedby="dialog-description"
        >
          {selectedCard && (
            <React.Fragment>
              <DialogTitle id="dialog-title">
                {selectedCard.heading}
              </DialogTitle>
              <DialogContent>
                <Typography>{selectedCard.details}</Typography>
              </DialogContent>
            </React.Fragment>
          )}
        </Dialog>
      </main>
    </ThemeProvider>
  );
}
