import React from "react";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
// import Link from "@mui/material/Link";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Navbar from "../Layout/Header";
import Footer from "../Layout/Footer";
import Image from "../image/img3.png";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
const useStyles = makeStyles((theme) => ({
  form: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(2),
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "25ch",
    },
    "& .MuiButton-root": {
      margin: theme.spacing(2),
    },
  },
}));

const styles = {
  paperContainer: {
    backgroundImage: `url(${Image})`,
    backgroundPosition: "center",
  },
};

const theme = createTheme();

export default function Album() {
  const classes = useStyles();
  document.title = "Contact";

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      <main>
        {/* Hero unit */}
        <Box style={styles.paperContainer}>
          <Navbar />
          <Box
            color="transparent"
            sx={{
              pt: 10,
              pb: 10,
            }}
          >
            <Container >
              <Typography
                component="h1"
                variant="h2"
                align="center"
                color="common.white"
                gutterBottom
              >
                CONTACT
              </Typography>
              <Typography
                variant="h5"
                align="center"
                color="common.white"
                paragraph
              >
                At Luxury Hotels, we take our customers seriously. Do you have
                any enquiries, compaints or requests, please forward it to our
                support desk and we will get back to you as soon as possible.
              </Typography>
            </Container>
          </Box>
        </Box>

        <form className={classes.form}>
          <TextField label="Name" variant="outlined" required />
          <TextField label="Email" variant="outlined" type="email" required />
          <TextField
            label="Message"
            variant="outlined"
            multiline
            rows={4}
            required
          />
          <Button variant="contained" color="primary">
            Send
          </Button>
        </form>
      </main>
      {/* Footer */}
      <Footer />

      {/* End footer */}
    </ThemeProvider>
  );
}
