import React, { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Navbar from "../Layout/Header";
import Image from "../image/img5.png";
import Button from "@mui/material/Button";
import Swal from "sweetalert2";
import instance from "../axios.create";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Grid from "@mui/material/Grid";
import Room1 from "../image/room1.jpg";
import Modal from "@mui/material/Modal";
import QRCode from "qrcode.react";
const style_ = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 4,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};
const styles = {
  paperContainer: {
    backgroundImage: `url(${Image})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
};

const theme = createTheme();
const color = "#14274A";

export default function Profile() {
  const [rows, setrows] = useState([]);
  const [open, setOpen] = React.useState(false);
  const [randomNumber, setRandomNumber] = useState("");

  const handleClose = () => setOpen(false);

  useEffect(() => {
    const token = localStorage.getItem("token");
    instance
      .post(
        "authen",
        {},
        {
          headers: {
            Authorization: `Basic ${token}`,
          },
        }
      )
      .then((response) => {
        console.log(response);
        if (response.data.status === "ok") {
          const temp = response.data.decoded.email;
          const JsonData = {
            email: temp,
          };
          instance.post("myroom", JsonData).then((response) => {
            console.log(response.data);
            setrows(response.data);
          });
        }else {
          Swal.fire({
            title: 'Authen Time Out',
            text: 'Please Login agin',
            icon: 'warning',
            confirmButtonText: 'OK'
          }).then((result) => {
            if (result.isConfirmed) {
              localStorage.removeItem("token");
            }
          });
        }
      });
  }, []);
  console.log();
  const days = [
    "Sunday",
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
  ];

  function check(temp) {
    const checkStatus = temp;
    const buttonColor = checkStatus ? "error" : "success";
    return buttonColor;
  }

  function check1(temp) {
    const checkStatus = temp;
    const Buttonname = checkStatus
      ? ""
      : "";
    return Buttonname;
  }
  const handleClick = (param) => {
    setOpen(true);
    setRandomNumber(param);
  };

  return (
    <ThemeProvider theme={theme}>
      <main>
        {/* Hero unit */}
        <Box style={styles.paperContainer}>
          <Navbar />
          <Box
            color="transparent"
            elevation={0}
            sx={{
              pt: 5,
              pb: 10,
            }}
          >
            <Typography
              variant="h2"
              align="center"
              color="common.white"
              gutterBottom
            >
              MY Room
            </Typography>
          </Box>
        </Box>
        <Box
          sx={{
            backgroundColor: "#14274A",
            pt: 3,
            pb: 8,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              bgcolor: "background.paper",
              boxShadow: 10,
              borderRadius: 2,
              p: 3,
              ml: 4,
              mr: 4,
              maxWidth: "sm",
            }}
          >
            <Grid
              container
              spacing={4}
              sx={{
                pt: 5,
              }}
            >
              {rows.map((row) => (
                <Grid item key={row.id_book} xs={12} sm={12} md={12}>
                  <Card
                    sx={{
                      height: "100%",
                      display: "flex",
                      flexDirection: "column",
                    }}
                  >
                    <CardMedia component="img" image={Room1} alt="random" />
                    <CardContent
                      sx={{
                        flexGrow: 1,
                        bgcolor: color,
                        textAlign: "center",
                        color: "#f0f0f0",
                      }}
                    >
                      <Typography gutterBottom variant="h5" component="h2">
                        {row.name_room}
                      </Typography>
                    </CardContent>
                    <CardContent sx={{ flexGrow: 1 }}>
                      <Typography>{row.detill_room}</Typography>
                      <Typography level="body3" sx={{ py: 2 }}>
                        Check in{" "}
                        {days[new Date(row.checkin_room).getDay()] +
                          " " +
                          new Date(row.checkin_room).getDate() +
                          "/" +
                          new Date(row.checkin_room).getMonth() +
                          "/" +
                          new Date(row.checkin_room).getFullYear()}
                      </Typography>
                      <Typography level="body3" sx={{ py: 1 }}>
                        Check out{" "}
                        {days[new Date(row.checkout_room).getDay()] +
                          " " +
                          new Date(row.checkout_room).getDate() +
                          "/" +
                          new Date(row.checkout_room).getMonth() +
                          "/" +
                          new Date(row.checkout_room).getFullYear()}
                      </Typography>
                      <Typography level="body3" sx={{ py: 1 }}>
                        {/* Price Total {row.price * test()}
                        {sessionStorage.setItem("price", row.price * test())} */}
                      </Typography>
                    </CardContent>
                    <CardActions disableSpacing>
                      <Button color={check(row.status_room)} >
                        {check1(row.status_room)}
                      </Button>
                      <Button
                        variant="contained"
                        sx={{ borderRadius: 5, ml: "auto" }}
                        onClick={() => handleClick(row.password_room)}
                      >
                        
                      </Button>
                    </CardActions>
                  </Card>
                </Grid>
              ))}
            </Grid>
            <Modal open={open} onClose={handleClose}>
              <Box sx={style_}>
                <Typography variant="h6" component="h2">
                  password is = {randomNumber}
                </Typography>

                <QRCode value={randomNumber} />
              </Box>
            </Modal>
          </Box>
        </Box>
      </main>
    </ThemeProvider>
  );
}
