import React, { useEffect, useState } from "react";
import { Button, Box, Grid } from "@mui/material";
import TextField from "@mui/material/TextField";
import { useNavigate } from "react-router-dom";

export default function Userdata() {
  sessionStorage.removeItem("User_data");
  const navigate = useNavigate();

  const [error, setError] = useState(null);

  useEffect(() => {}, []);

  const handleSubmit = (event) => {
    event.preventDefault();

    const data = new FormData(event.currentTarget);

    const JsonData = {
      id: data.get("id"),
      name: data.get("firstName"),
      lastname: data.get("lastName"),
      email: data.get("email"),
      tel: data.get("tel"),
    };

    // Check if any required fields are empty
    if (!JsonData.id || !JsonData.name || !JsonData.lastname || !JsonData.email || !JsonData.tel) {
      setError("Please fill in all required fields.");
      return;
    }

    const data_set = JSON.stringify(JsonData);
    sessionStorage.setItem("User_data", data_set);

    navigate("/dashboard/walk_check_in/5/2");
  };

  return (
    <React.Fragment>
      <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <TextField
              required
              fullWidth
              id="id"
              label="ID Card number"
              name="id"
              autoComplete="id"
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              autoComplete="given-name"
              name="firstName"
              required
              fullWidth
              id="firstName"
              label="First Name"
              autoFocus
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              id="lastName"
              label="Last Name"
              name="lastName"
              autoComplete="family-name"
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              required
              fullWidth
              id="tel"
              label="telophone"
              name="tel"
              autoComplete="tel"
            />
          </Grid>
        </Grid>
        {error && (
          <p style={{ color: "red", marginTop: "10px" }}>{error}</p>
        )}
        <Button
          type="submit"
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 2 }}
        >
          
        </Button>
      </Box>
    </React.Fragment>
  );
}
