import React, { useEffect, useState } from "react";
// import Link from "@mui/material/Link";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import { format } from "date-fns";
import Modal from "@mui/material/Modal";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { Button } from "@mui/material";
import instance from "../axios.create";
import QRCode from "qrcode.react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import { InputAdornment, TextField } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
const style_ = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  boxShadow: 24,
  p: 4,
  borderRadius: 4,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
};

export default function Roomdata() {
  document.title = "Room data";
  const navigate = useNavigate();
  const [selectedRoom, setSelectedRoom] = useState("");
  const [searchTerm, setSearchTerm] = useState("");

  const [rows, setrows] = useState([]);
  const [type, settype] = useState([]);
  const [randomNumber, setRandomNumber] = useState("");
  const [open, setOpen] = useState(false);
  const handleClose = () => setOpen(false);

  useEffect(() => {
    instance.get("bookingdata").then((response) => {
      setrows(response.data);
    });
    instance.get("typeroom").then((response) => {
      settype(response.data);
    });
  }, []);

  function check(temp) {
    const checkStatus = temp;
    const buttonColor = checkStatus ? "red" : "green";
    return buttonColor;
  }
  function check1(temp) {
    const checkStatus = temp;
    const Buttonname = checkStatus
      ? " "
      : " ";
    return Buttonname;
  }
  const handleClick = (param) => {
    setOpen(true);
    setRandomNumber(param);
  };

  // Add a function to handle selecting a Room Name
  const handleSelectRoom = (value) => {
    setSelectedRoom(value);
  };

  const handleSearch = (e) => {
    setSearchTerm(e.target.value);
  };

  const statusclick = (status, id_booking) => {
    if (status === 0) {
      Swal.fire({
        title: "",
        icon: "info",
      });
    } else {
      Swal.fire({
        title: "?",
        text: " : " + id_booking + " ",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "checkout",
      }).then((result) => {
        if (result.isConfirmed) {
          const status_room_update = {
            id_booking: id_booking,
          };
          instance
            .put("update_booking_status", status_room_update)
            .then((response) => {
              if (response.data.status === "ok") {
                Swal.fire(
                  "!",
                  "",
                  "success"
                );
                setTimeout(function () {
                  navigate(0);
                }, 1000);
              } else {
                Swal.fire("!", "", "warning");
                setTimeout(function () {
                  navigate(0);
                }, 1000);
              }
            });
        }
      });
    }
  };

  return (
    <React.Fragment>
      <div style={{ display: "flex", alignItems: "center" ,padding:10}}>
        <label htmlFor="room-select">Select Room Name:</label>
        <select
          id="room-select"
          value={selectedRoom}
          onChange={(e) => handleSelectRoom(e.target.value)}
          style={{ marginRight: "10px" }}
        >
          <option value="">All Rooms</option>
          {type.map((room) => (
            <option key={room.id} value={room.path}>
              {room.nameroom}
            </option>
          ))}
        </select>
        <TextField
          label="Search by ID Booking or Email User"
          value={searchTerm}
          onChange={handleSearch}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
          style={{ width: '320px' }}
        />
      </div>

      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>ID_Booking</TableCell>
            <TableCell>ID_Room</TableCell>
            <TableCell>Room_Name</TableCell>
            <TableCell>Email_User</TableCell>
            <TableCell sx={{ pr: 8 }}>Check_in</TableCell>
            <TableCell sx={{ pr: 8 }}>Check_out</TableCell>
            <TableCell>Price Total</TableCell>
            <TableCell sx={{ pr: 10 }}>Status_Room</TableCell>
            <TableCell sx={{ pr: 10 }}>password</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows
            .filter((row) => {
              // If no Room Name is selected, show all rows
              if (selectedRoom === "") {
                return true;
              }
              // Otherwise, only show rows with the selected Room Name
              return row.room_name === selectedRoom;
            })
            .filter((row) => {
              // If no search term is entered, show all rows
              if (searchTerm === "") {
                return true;
              }
              // Otherwise, filter rows by the search term
              const lowerSearchTerm = searchTerm.toLowerCase();
              return (
                row.id_booking
                  .toString()
                  .toLowerCase()
                  .includes(lowerSearchTerm) ||
                row.email_user.toLowerCase().includes(lowerSearchTerm)
              );
            })
            .map((row) => (
              <TableRow key={row.id_booking}>
                <TableCell>{row.id_booking}</TableCell>
                <TableCell>{row.id_room}</TableCell>
                <TableCell>{row.room_name}</TableCell>
                <TableCell>{row.email_user}</TableCell>
                <TableCell>
                  {format(new Date(row.checkin), "eee dd/MM/yyyy")}
                </TableCell>
                <TableCell>
                  {format(new Date(row.checkout), "eee dd/MM/yyyy")}
                </TableCell>
                <TableCell>{row.price_total}</TableCell>
                <TableCell
                  style={{ color: check(row.status_room) }}
                  onClick={() => statusclick(row.status_room, row.id_booking)}
                >
                  {check1(row.status_room)}
                </TableCell>
                <TableCell>
                  <Button
                    variant="contained"
                    color="secondary"
                    sx={{ borderRadius: 5 }}
                    onClick={() => handleClick(row.password_room)}
                  >
                    
                  </Button>
                </TableCell>
              </TableRow>
            ))}
        </TableBody>
      </Table>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style_}>
          <Typography variant="h6" component="h2">
            password is = {randomNumber}
          </Typography>
          <QRCode value={randomNumber} />
        </Box>
      </Modal>
      <style>{`
        table {
          border-collapse: separate;
          border-spacing: 0;
          min-width: 800px;
          width: 100%;
        }
        th {
          background-color: #f5f5f5;
          border: 1px solid #ddd;
          font-weight: bold;
          padding: 10px;
          text-align: left;
        }
        td {
          border: 1px solid #ddd;
          padding: 10px;
        }
        tr:nth-child(even) td {
          background-color: #f5f5f5;
        }
        tr:hover td {
          background-color: #fafafa;
        }
      `}</style>
    </React.Fragment>
  );
}
