import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import Navbar from "../Layout/Header";
import Swal from "sweetalert2";
import instance from "../axios.create";
import { useNavigate } from "react-router-dom";
import { Link as RouterLink } from "react-router-dom";

const theme = createTheme();
export default function SignUp() {
  const [formErrors, setFormErrors] = React.useState({});

  document.title = "Register";
  const navigate = useNavigate();

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);

    const JsonData = {
      id: data.get("id"),
      name: data.get("firstName"),
      lastname: data.get("lastName"),
      email: data.get("email"),
      tel: data.get("tel"),
      password: data.get("password"),
    };

    // Validate form fields
    const errors = {};
    if (!JsonData.id) {
      errors.id = "ID Card number is required";
    }
    if (!JsonData.name) {
      errors.firstName = "First Name is required";
    }
    if (!JsonData.lastname) {
      errors.lastName = "Last Name is required";
    }
    if (!JsonData.email) {
      errors.email = "Email Address is required";
    }
    if (!JsonData.tel) {
      errors.tel = "Telephone is required";
    }
    if (!JsonData.password) {
      errors.password = "Password is required";
    }
    setFormErrors(errors);

    // If there are no errors, submit the form
    if (Object.keys(errors).length === 0) {
      instance.post("register", JsonData).then((response) => {
        if (response.data.status === true) {
          Swal.fire({
            title: "Register success",
            icon: "success",
          });
          setTimeout(function () {
            navigate("/login");
          }, 1000);
        } else {
          Swal.fire({
            title: "Register failed",
            icon: "error",
            confirmButtonText: "OK",
          });
        }
      });
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Box
        sx={{
          backgroundColor: "#14274A",
        }}
      >
        <Navbar />
      </Box>

      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <Box
            component="form"
            noValidate
            onSubmit={handleSubmit}
            sx={{ mt: 3 }}
          >
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="id"
                  label="ID Card number"
                  name="id"
                  autoComplete="id"
                  error={!!formErrors.id}
                  helperText={formErrors.id}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="given-name"
                  name="firstName"
                  required
                  fullWidth
                  id="firstName"
                  label="First Name"
                  autoFocus
                  error={!!formErrors.firstName}
                  helperText={formErrors.firstName}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="lastName"
                  label="Last Name"
                  name="lastName"
                  autoComplete="family-name"
                  error={!!formErrors.lastName}
                  helperText={formErrors.lastName}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                  error={!!formErrors.email}
                  helperText={formErrors.email}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="tel"
                  label="Telephone"
                  name="tel"
                  autoComplete="tel"
                  error={!!formErrors.tel}
                  helperText={formErrors.tel}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                  error={!!formErrors.password}
                  helperText={formErrors.password}
                />
              </Grid>
             
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign Up
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link to="/login" variant="body2" component={RouterLink}>
                  Already have an account? Sign in
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
